




















import Vue from "vue";
export default Vue.extend({
  name: "Header",
  data() {
    return {
      HomeList: [
        {
          name: "Home",
          id: 0
        },
        {
          name: "SignIn",
          id: 1
        },
        {
          name: "SignOut",
          id: 2
        }
      ],
      curentIndex: 0
    };
  },
  mounted() {
    if (this.$route.query.from === "person") {
      this.$refs.setActive.$el.className = "nuxt-link-exact-active";
    }
  },
  computed: {
    hasToken() {
      return this.$store.state.user ? true : false
    }
  }
});
